"use client"
import React from 'react'
import Image from 'next/image'
import { venueImages } from '../content/site'

export default function VenueStrip({ count = 3 }: { count?: number }) {
  const images = venueImages.slice(0, count)
  return (
    <div className="grid grid-cols-3 gap-2">
      {images.map((src, idx) => (
        <figure key={src} className="relative h-28 overflow-hidden rounded">
          <Image src={src} alt={`Venue interior ${idx + 1}`} fill style={{ objectFit: 'cover' }} sizes="(max-width: 640px) 100vw, 33vw" />
          <figcaption className="sr-only">Interior shot {idx + 1}</figcaption>
          <div className="absolute inset-0 bg-black/20" aria-hidden="true"></div>
        </figure>
      ))}
    </div>
  )
}
