"use client"
import React from 'react'
import { tracks } from '../content/site'
import { useAudio } from './AudioProvider'

export default function PlayerList() {
  const audio = useAudio()
  return (
    <div className="mt-4 grid grid-cols-1 sm:grid-cols-2 gap-4">
      {tracks.map((t) => (
        <article key={t.id} className="p-4 border rounded border-neutral-800">
          <div className="flex items-center justify-between">
            <div>
              <div className="font-medium">{t.title}</div>
              <div className="text-xs text-neutral-400">{t.artist}</div>
            </div>
            <div>
              <button onClick={() => audio.playTrack(t)} className="px-3 py-2 rounded bg-neutral-700 focus-ring">Play</button>
            </div>
          </div>
        </article>
      ))}
    </div>
  )
}
