"use client"
import React from 'react'
import Image from 'next/image'

export default function Hero({ image, title, subtitle }: { image?: string; title: string[]; subtitle?: string }) {
  return (
    <header className="relative h-[48vh] sm:h-[56vh] rounded overflow-hidden mb-8">
      {image && (
        <div className="absolute inset-0 -z-10">
          <Image src={image} fill alt="" style={{ objectFit: 'cover', objectPosition: 'center' }} sizes="(max-width: 768px) 100vw, 80vw" priority />
          <div className="absolute inset-0 bg-black/40" aria-hidden="true" />
        </div>
      )}
      <div className="relative z-10 h-full flex flex-col justify-center container">
        <h1 className="text-5xl sm:text-6xl md:text-7xl font-extrabold leading-tight text-white">{title.map((t, i) => <span key={t}>{t}{i < title.length-1 && <br />}</span>)}</h1>
        {subtitle && <p className="mt-4 text-neutral-200">{subtitle}</p>}
      </div>
    </header>
  )
}
